*{font-family:'Poppins'}.row{margin-top:2.3em}.col{font-family:'Poppins';align-items:center;justify-content:center;display:flex;flex-direction:column;flex-wrap:wrap;text-align:center;background:rgba(255,255,255,0.2);padding:0 2em 1em;margin:0 20px;border-radius:3em;position:relative;backdrop-filter:blur(40px);border:solid 2px transparent;background-clip:padding-box;box-shadow:10px 10px 10px rgba(46,54,68,0.03)}.col .image{transition:0.5s;position:relative;display:flex;align-items:center;justify-content:center}.col .image.image-1{transform:translateY(7.5em);z-index:1}.col .image.image-1 .case-studies__image{transition:.5s;opacity:.5;color:white}.col .image.image-2{visibility:hidden}.col:hover .image.image-1 .case-studies__image{opacity:1}.col:hover .image.image-1 .content{opacity:0.2;transition:0.5s}.col:hover .image.image-1 .content img{max-width:100px}.col:hover .image.image-1 .content h3{margin:10px 0 0;padding:0;color:#fff;text-align:center;font-size:1.5em}.col:hover .image.image-1,.col:hover .image.image-2{transform:translateY(0);visibility:visible}.col .image.image-2{transform:translateY(-7.5em);box-sizing:border-box}.col .image.image-2 .content{width:90%;overflow:hidden;text-align:center;box-sizing:border-box}.subtitle-info{font-family:'Poppins';font-size:25px;color:#777777;font-weight:400;margin:0%;margin-top:1em;text-align:left}.case-studies__b{border:1px solid #5A288E;border-radius:30px;display:flex;justify-self:center;justify-content:center;margin:1.5em 0;align-content:center;cursor:pointer}.case-studies__b .button-text{font-size:1.2rem;color:#5A288E;font-weight:400;margin:3% 0;text-align:center}.case-studies__image{cursor:pointer;position:relative;display:flex;flex-direction:column;justify-content:center;background-size:cover;transition:ease-in-out 0.3s;border-radius:0 !important;background-position:center;background-size:40%;background-repeat:no-repeat;width:280px;z-index:2}.contact{font-family:"Poppins", sans-serif;display:flex;flex-direction:column;align-items:center}.contact .contact-title{font-size:35px;color:#6c6c6c;font-weight:700;padding:20px 0}.contact .contact-subtitle{font-size:25px;color:#6c6c6c;padding-bottom:0.5rem}.contact .email{color:#007882;font-size:25px}@media screen and (min-device-width: 768px) and (max-device-width: 992px){.left-info{left:90px}.left-info .case-studies__b .button-text{font-size:1rem}.right-info .case-studies__image{width:240px}}@media screen and (min-device-width: 576px) and (max-device-width: 768px){.left-info{left:0px;position:relative}.left-info .case-studies__b{width:190px;margin-right:0;margin-left:0;position:absolute;transform:translateY(290%)}.left-info .case-studies__b .button-text{font-size:1rem}.left-info .subtitle-info{padding:0;position:absolute;transform:translateY(50%)}.right-info{width:200px !important}.right-info .case-studies__image{width:200px}}@media screen and (min-device-width: 320px) and (max-device-width: 576px){.left-info{display:none}.right-info{width:150px !important;margin-right:0;display:flex;justify-content:center}.right-info .case-studies__image{width:200px;margin:30px 0}}
