.header-container{display:flex;background-color:rgba(255,255,255,0.8);padding:20px 80px;flex-direction:row;position:sticky;top:0;z-index:100}.header-container .logo{cursor:pointer;display:block;align-self:start;width:150px;height:150px;background:url(../../assets/logo.svg);background-size:150px 150px}.header-container .menu{float:right;display:flex;flex-grow:1;justify-content:space-evenly;align-items:center;padding-top:30px;flex-direction:row;font-family:"Poppins"}.header-container .menu .portfolio{font-family:"Poppins";font-weight:500;color:#DA5B4D;text-decoration:none;background-color:transparent;font-size:18px;border:none;cursor:pointer}.header-container .menu .portfolio:focus{outline:transparent}.header-container .menu .portfolio:active{outline:transparent}.header-container .menu :visited{text-decoration:underline}.header-container .menu :hover{text-decoration:underline;color:#F08B3D}.header-container .menu .aboutme{font-family:"Poppins", sans-serif;font-weight:6500;color:#DA5B4D;background-color:transparent;font-size:18px;border:none;cursor:pointer}.header-container .menu .aboutme:focus{outline:transparent}.header-container .menu .aboutme:active{outline:transparent}.header-container .menu [aria-pressed="true"]{color:#DA5B4D}.header-container .menu :hover{text-decoration:underline;color:#F08B3D}@media screen and (min-device-width: 600px) and (max-device-width: 768px){.header-container{padding:20px 80px 0 50px}.header-container .logo{width:100px;height:100px;background-size:100px 100px}.header-container .menu{flex-direction:row}.header-container .menu .portfolio{font-size:16px}.header-container .menu .aboutme{font-size:16px}}@media screen and (min-device-width: 480px) and (max-device-width: 600px){.header-container{padding:20px 30px}.header-container .logo{width:70px;height:70px;background-size:70px 70px}.header-container .menu{flex-direction:row}.header-container .menu .portfolio{font-size:16px}.header-container .menu .aboutme{font-size:16px}}@media screen and (min-device-width: 380px) and (max-device-width: 480px){.header-container{padding:20px 50px}.header-container .logo{width:60px;height:60px;background-size:60px 60px}.header-container .menu{flex-direction:row;padding-top:0}.header-container .menu .tabs{display:flex;align-self:center}.header-container .menu .portfolio{font-size:14px}.header-container .menu .aboutme{font-size:14px}.header-container .buttons{padding-top:0}}@media screen and (max-device-width: 465px) and (min-device-width: 380px){.header-container .logo{width:60px;height:60px;background-size:60px 60px}.header-container .menu{padding-top:0;flex-direction:column;justify-content:space-around}.header-container .menu .tabs{display:flex;align-self:flex-end}.header-container .menu .portfolio{font-size:12px}.header-container .menu .aboutme{font-size:12px}}@media screen and (min-device-width: 320px) and (max-device-width: 380px){.header-container{padding:20px 40px}.header-container .logo{width:50px;height:50px;background-size:50px 50px}.header-container .menu{padding-top:0;flex-direction:column;justify-content:space-around}.header-container .menu .tabs{display:flex;align-self:flex-end}.header-container .menu .portfolio{font-size:12px}.header-container .menu .aboutme{font-size:12px}}
